___CSS_LOADER_EXPORT___.push([module.id, `/* 1. Use a more-intuitive box-sizing model */
*,
*::before,
*::after {
	box-sizing: border-box;
}

/* 2. Remove default margin */
* {
	margin: 0;
}

body {
	/* 3. Add accessible line-height */
	line-height: 1.5;
	/* 4. Improve text rendering */
	-webkit-font-smoothing: antialiased;
}

/* 5. Improve media defaults */
img,
picture,
video,
canvas,
svg {
	display: block;
	max-width: 100%;
}

/* 6. Inherit fonts for form controls */
input,
button,
textarea,
select {
	font: inherit;
}

/* 7. Avoid text overflows */
p,
h1,
h2,
h3,
h4,
h5,
h6 {
	overflow-wrap: break-word;
}

/* 8. Improve line wrapping */
p {
	text-wrap: pretty;
}

h1,
h2,
h3,
h4,
h5,
h6 {
	text-wrap: balance;
}

/*
  9. Create a root stacking context
*/
#root,
#__next {
	isolation: isolate;
}



/* PROJECT CSS */

.form-container {
	background-color: green;
	display: inline-block;
	padding: 30px;
}

form {
	background-color: pink;
	padding: 10px;
	display: flex;
	flex-direction: column;
	gap: 7px;
	width: 400px;
}

form>div {
	display: grid;
	grid-template-columns: 1fr 1fr;
	grid-template-rows: 32px 1fr;
}

button {
	width: fit-content;
	margin: auto;
}

input,
select {
	width: 200px;
	border: none;
	padding: 2px 5px;
	border-radius: 5px;
	height: 32px;
}

input:invalid,
select:invalid {
	border: 2px solid red;
	background-color: #fdd;
}

input:valid,
select:valid {
	border: 2px solid rgb(6, 85, 6);
	background-color: rgba(172, 255, 47, 0.616);
}

.error-msg {
	grid-area: 2 / 2;
	background-color: rgba(0, 0, 0, 0.274);
	color: rgb(121, 5, 5);
}`, "",{"version":3,"sources":["webpack://./src/styles.css"],"names":[],"mappings":"AAAA,6CAA6C;AAC7C;;;CAGC,sBAAsB;AACvB;;AAEA,6BAA6B;AAC7B;CACC,SAAS;AACV;;AAEA;CACC,kCAAkC;CAClC,gBAAgB;CAChB,8BAA8B;CAC9B,mCAAmC;AACpC;;AAEA,8BAA8B;AAC9B;;;;;CAKC,cAAc;CACd,eAAe;AAChB;;AAEA,uCAAuC;AACvC;;;;CAIC,aAAa;AACd;;AAEA,4BAA4B;AAC5B;;;;;;;CAOC,yBAAyB;AAC1B;;AAEA,6BAA6B;AAC7B;CACC,iBAAiB;AAClB;;AAEA;;;;;;CAMC,kBAAkB;AACnB;;AAEA;;CAEC;AACD;;CAEC,kBAAkB;AACnB;;;;AAIA,gBAAgB;;AAEhB;CACC,uBAAuB;CACvB,qBAAqB;CACrB,aAAa;AACd;;AAEA;CACC,sBAAsB;CACtB,aAAa;CACb,aAAa;CACb,sBAAsB;CACtB,QAAQ;CACR,YAAY;AACb;;AAEA;CACC,aAAa;CACb,8BAA8B;CAC9B,4BAA4B;AAC7B;;AAEA;CACC,kBAAkB;CAClB,YAAY;AACb;;AAEA;;CAEC,YAAY;CACZ,YAAY;CACZ,gBAAgB;CAChB,kBAAkB;CAClB,YAAY;AACb;;AAEA;;CAEC,qBAAqB;CACrB,sBAAsB;AACvB;;AAEA;;CAEC,+BAA+B;CAC/B,2CAA2C;AAC5C;;AAEA;CACC,gBAAgB;CAChB,sCAAsC;CACtC,qBAAqB;AACtB","sourcesContent":["/* 1. Use a more-intuitive box-sizing model */\n*,\n*::before,\n*::after {\n\tbox-sizing: border-box;\n}\n\n/* 2. Remove default margin */\n* {\n\tmargin: 0;\n}\n\nbody {\n\t/* 3. Add accessible line-height */\n\tline-height: 1.5;\n\t/* 4. Improve text rendering */\n\t-webkit-font-smoothing: antialiased;\n}\n\n/* 5. Improve media defaults */\nimg,\npicture,\nvideo,\ncanvas,\nsvg {\n\tdisplay: block;\n\tmax-width: 100%;\n}\n\n/* 6. Inherit fonts for form controls */\ninput,\nbutton,\ntextarea,\nselect {\n\tfont: inherit;\n}\n\n/* 7. Avoid text overflows */\np,\nh1,\nh2,\nh3,\nh4,\nh5,\nh6 {\n\toverflow-wrap: break-word;\n}\n\n/* 8. Improve line wrapping */\np {\n\ttext-wrap: pretty;\n}\n\nh1,\nh2,\nh3,\nh4,\nh5,\nh6 {\n\ttext-wrap: balance;\n}\n\n/*\n  9. Create a root stacking context\n*/\n#root,\n#__next {\n\tisolation: isolate;\n}\n\n\n\n/* PROJECT CSS */\n\n.form-container {\n\tbackground-color: green;\n\tdisplay: inline-block;\n\tpadding: 30px;\n}\n\nform {\n\tbackground-color: pink;\n\tpadding: 10px;\n\tdisplay: flex;\n\tflex-direction: column;\n\tgap: 7px;\n\twidth: 400px;\n}\n\nform>div {\n\